
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  }
  

  #myVideo {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%; 
    min-height: 100%;

  }
  
  .container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
 
  }
.ty{
  margin-top: 13px;
  color: white;
  font-size: 18px;
}


  .box {
  width: 25%;
  height: 60vh;
  background-color: black;
  opacity: 0.7;
  margin: auto;
  margin-top: 100px;
  border-radius: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  }
  .inputData {
  padding: 20px;
  
  }

  .inputFeild{
    margin-top: 20px;
    border-radius: 5px;
    height: 35px;
    width: 180px;
    margin-left: 90px;
    
  }
  .inputData input {
  margin-top: 10px;
  padding: 20px;
  width: 200%;
  outline: none;
  border: none;
  font-size: 30px;
  border-radius: 120px;
  }
 
  i {
  font-size: 60px;
  color: white;
  margin-right: 10px;
  }
  .info {
  margin-top: 10%;
  margin-left: 28%;
  color: white;
  }
  .info .temp {
  font-size: 30px;
  color: white;
  margin: 20px 0 20px 0;
  }
  
  .info h3 {
  font-size: 15px;
  font-weight: 400;
  color: white;
  }
  .wave1 {
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -12rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
  }
  .wave2 {
  animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.2;
  }
  .wave3 {
  animation: drift 7000ms infinite linear;
  z-index: 3 !important;
  opacity: 0.1;
  }
  .box::after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
  }
  @keyframes drift {
  from {
  transform: rotate(0deg);
  }
  from {
  transform: rotate(360deg);
  }
  }

  .location{
    font-family: 'Concert One', cursive;
    font-size: 45px;
  }

.errormsg{
  text-align: center;
  margin-top: 50px;
}



@media screen and (max-width: 1000px) {
  .box{
    height: 500px;
    width: 350px;
  }
  
}


body{
  background-image: url(./ttt.jpg);
}